@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
html {
  scroll-behavior: smooth;
}

#root {
  background: #f0f0f0;
}

.jumbotron {
  background: url(/static/media/hero.b2def230.png), #f0f0f0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 600px;
}

.menu-button {
  border-radius: 50%;
}

.phone-card {
  width: 20rem;
  height: 37rem;
  background-color: black;
  border-radius: 25px;
}

.phone-footer {
  text-align: center;
}

.dropdown-toggle::after {
  display: none;
}

.side-button {
  width: 200px;
}

.video-container {
  /* background-image: url("./assets/img/1.png");
  background-repeat: no-repeat;
  background-position: initial; */
  /* height: 600px;
  background-color: white; */
  margin-top: 200px;
}

.illustration {
  width: 100%;
  height: auto;
}

.illustration-container {
  height: 600px;
  width: 300px;
}

.heading {
  font-weight: bolder;
  font-size: 50px;
  text-align: center;
  font-family: "Montserrat", sans-serif;
}

.nav-link {
  color: #fefefe;
  font-size: 25px;
}

.nav-link:hover {
  color: #161712;
}

.information-container {
  margin-top: 350px;
}

.map-illustration {
  height: 500px;
  width: auto;
}

@media screen and (min-width: 992px) {
  .info-text {
    font-family: "Poppins", sans-serif;
    color: #2db548;
    font-size: 3vw;
    font-weight: 600;
  }

  .info-text-secondary {
    font-family: "Poppins", sans-serif;
    color: #ebaa17;
    font-size: 3vw;
    font-weight: 600;
  }
}

@media screen and (max-width: 992px) {
  .info-text {
    font-family: "Montserrat", sans-serif;
    color: #2db548;
    font-size: 7vw;
    text-align: center;
  }

  .info-text-secondary {
    font-family: "Montserrat", sans-serif;
    color: #ebaa17;
    font-size: 7vw;
    text-align: center;
  }
}

.anim-container-right {
  border-style: solid;
  border-color: #ebaa17;
  border-radius: 50px;
  background-color: white;
  margin-bottom: 5px;
}

.anim-container-left {
  border-style: solid;
  border-color: #2db548;
  border-radius: 50px;
  background-color: #f0f0f0;
  margin-bottom: 5px;
}

@media screen and (min-width: 992px) {
  .tag-line {
    font-size: 3vw;
    font-family: "Montserrat", sans-serif;
    color: #fefefe;
    padding-left: 60px;
  }
  .anim-button {
    border-radius: 10px;
  }
}

@media screen and (max-width: 992px) {
  .tag-line {
    font-size: 8vw;
    font-family: "Montserrat", sans-serif;
    color: #fefefe;
    padding-left: 60px;
  }
  .anim-button {
    border-radius: 10px;
    margin-top: 400px;
  }
}

.map-h2 {
  text-transform: uppercase;
  font-size: 1rem;
  padding: 20px;
  padding-left: 10px;
  text-align: center;
}

.google-map {
  width: 100%;
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: var(--main-blue);
}

.pin-icon {
  font-size: 4rem;
}

.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 80vh;
  }

  .map-h2 {
    font-size: 1.3rem;
    font-weight: 400;
  }

  .pin {
    width: 15vw;
  }

  .pin-icon {
    font-size: 10vw;
  }
}

